.login-modal {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  padding: var(--spacing-md);
  opacity: 1;
  transition: opacity var(--transition-normal);
}

.login-modal.hidden {
  opacity: 0;
  pointer-events: none;
}

/* Контейнер содержимого */
.login-content {
  background-color: var(--surface-color);
  padding: var(--spacing-xl);
  border-radius: var(--radius-lg);
  width: 100%;
  max-width: 400px;
  box-shadow:
    0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  animation: modal-in var(--transition-normal) cubic-bezier(0.4, 0, 0.2, 1);
}

/* Заголовок */
.login-content h2 {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
  text-align: center;
  margin-bottom: var(--spacing-lg);
  padding-bottom: var(--spacing-sm);
  position: relative;
}

.login-content h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 2px;
  background-color: var(--primary-color);
}

/* Поле ввода */
.login-content input {
  width: 100%;
  padding: var(--spacing-md);
  background-color: var(--background-color);
  border: 2px solid var(--border-color);
  border-radius: var(--radius-md);
  color: var(--text-primary);
  font-size: 16px;
  margin-bottom: var(--spacing-md);
  transition: all var(--transition-fast);
}

.login-content input:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px var(--primary-light);
  outline: none;
}

.login-content input::placeholder {
  color: var(--text-secondary);
  opacity: 0.7;
}

/* Сообщение об ошибке */
.login-content .error-message {
  color: var(--error-color);
  font-size: 14px;
  margin-bottom: var(--spacing-md);
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--radius-sm);
  background-color: rgba(242, 139, 130, 0.1);
  border: 1px solid rgba(242, 139, 130, 0.2);
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  animation: shake 0.4s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

.login-content .error-message.hidden {
  display: none;
}

/* Кнопка входа */
.login-content button {
  width: 100%;
  padding: var(--spacing-md);
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: var(--radius-md);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-fast);
  position: relative;
  overflow: hidden;
}

.login-content button:hover {
  background-color: var(--primary-dark);
  transform: translateY(-1px);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.login-content button:active {
  transform: translateY(0);
  box-shadow: none;
}

.login-content button:disabled {
  background-color: var(--border-color);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Анимации */
@keyframes modal-in {
  0% {
    opacity: 0;
    transform: scale(0.95) translateY(10px);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

@keyframes shake {
  10%,
  90% {
    transform: translateX(-1px);
  }
  20%,
  80% {
    transform: translateX(2px);
  }
  30%,
  50%,
  70% {
    transform: translateX(-3px);
  }
  40%,
  60% {
    transform: translateX(3px);
  }
}

/* Загрузка */
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.login-content button.loading::after {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  top: calc(50% - 8px);
  left: calc(50% - 8px);
  border: 2px solid transparent;
  border-top-color: white;
  border-radius: 50%;
  animation: spin 0.6s linear infinite;
}

/* Адаптивность */
@media (max-width: 480px) {
  .login-content {
    padding: var(--spacing-lg);
    margin: var(--spacing-sm);
  }

  .login-content h2 {
    font-size: 20px;
  }

  .login-content input,
  .login-content button {
    font-size: 15px;
    padding: calc(var(--spacing-md) - 2px);
  }
}

/* Темная тема */
@media (prefers-color-scheme: dark) {
  .login-modal {
    background-color: rgba(0, 0, 0, 0.85);
  }

  .login-content {
    background-color: var(--surface-dark);
    box-shadow:
      0 10px 15px -3px rgba(0, 0, 0, 0.2),
      0 4px 6px -2px rgba(0, 0, 0, 0.15);
  }

  .login-content input {
    background-color: var(--background-dark);
    border-color: var(--border-dark);
  }

  .login-content input:focus {
    border-color: var(--primary-color);
    box-shadow: 0 0 0 3px var(--primary-dark);
  }

  .login-content .error-message {
    background-color: rgba(242, 139, 130, 0.05);
    border-color: rgba(242, 139, 130, 0.15);
  }
}
